import * as types from '../actionType';

const initialState = [{
	id: 1,
	name: 'Mobileapp',
	path: 'mobileapp',
	img: 'https://media.discordapp.net/attachments/592272227353886741/592272296475885599/mobileapp.jpg',
	technology: ['gulp', 'pug', 'scss', 'БЭМ-class', 'flexbox', 'smartgrid', 'jquery', 'SVG-sprite']
}];

export default function reducerModalContent(state = initialState, {type, payload}) {
	switch (type) {
		case types.SHOW_MODAL_CONTENT:
			return payload;
		case types.CLOSE_MODAL_CONTENT:
			return payload;
		default:
			return state;
	}
}