import React, {Component} from 'react';

import './header.scss';
import Level from "../level";
import Contacts from "../contacts";

class Header extends Component {

	state = {
	    showBlock: 'level'
	};

	getNameBtn = (e) => {
		this.updateState(e.target.name);
	};

	updateState = (name) => {
		if(name !== this.state.showBlock) {
			this.setState({
				showBlock: name
			});
			this.renderBlock(this.state.showBlock);
		}
	};

	renderBlock = (block = this.state.showBlock) => {
		if(block === 'level') {
			return <Level />
		}
		if(block === 'contacts') {
			return <Contacts />
		}
	};

	render() {
		return (
			<div className={'header'}>
				<div className="btn-block">
					<button name="level" onClick={this.getNameBtn}>Навыки</button>
					<button name="contacts" onClick={this.getNameBtn}>Контакты</button>
				</div>
				{
					this.renderBlock()
				}
			</div>
		);
	}
}

export default Header