import React from 'react';

import './modal-techno.scss';

const ModalTechno = ({data: {technology, bgColor, colorText}}) => {

	function renderListTechno(arr) {

		let n = 0,
				bg = '',
				color = '';

		return arr.map((item, index) => {
			if(bgColor !== undefined ) {
				if(n < bgColor.length) {
					color = colorText[n];
					bg = bgColor[n];
					n++;
				} else {
					n = 0;
					color = colorText[n];
					bg = bgColor[n];
					n++;
				}
			}
			return (
				<li key={index} style={{backgroundColor: bg, color: color}}>{item}</li>
			)
		})
	}

	return (
		<ul className="is-modal-content__list">
			{ renderListTechno(technology) }
		</ul>
	);
};

export default ModalTechno;