import React, {Component} from 'react';
import { connect } from "react-redux";
import { bindActionCreators } from 'redux'
import { showModal, showModelContent } from '../../actions';

import './content.scss'

class Content extends Component {

	showModal = (e) => {
		const result = this.props.db.filter(item => item.name === e.target.alt);
		this.props.showModal();
		this.props.showModelContent(result);
	};

	void = () => {
		const arr = [];
		const cols = 3;
		const residue = this.props.db.length % cols;
		const result = cols - residue;
		for(let n = 0; n < result; n++) {
			arr.push(Math.ceil(Math.random() * 10000))
		}
		return arr.map(item => (
			<div className="col-md-4 void-block" key={item}>
				<span>Для будущих работ</span>
			</div>
		))
	};

	render() {

		const { db } = this.props;

		const img = db.map(({miniImg, id, name}) => (
			<div className="col-md-4 img-block" key={id}>
				<img src={miniImg} alt={name} onClick={this.showModal}/>
			</div>
		));


		return (
			<div className="content">
				<div className="inner">
					<div className="row">
						{img}
						{this.void()}
					</div>
				</div>
			</div>
		);
	}
}

const mapStateToProps = state => {
	return {
		db: state.reducerDB,
		modal: state.reducerModal
	}
};

const mapDispatchToProps = dispatch => bindActionCreators(
	{
		showModal,
		showModelContent
	},
	dispatch
);

export default connect(mapStateToProps, mapDispatchToProps)(Content);