import React, {Component} from 'react';

import './modal.scss';
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import { closeModal, closeModalContent } from '../../actions';
import ModalContent from "../modal-content";


class Modal extends Component {

	onCloseModel = (e) => {
		if(e.target.id === 'modal') {
			this.props.closeModal();
			this.props.closeModalContent();
		}
	};


	render() {

		const { modalActive }  = this.props;

		const active = ( modalActive ) ? 'active' : '';

		return (
			<div className={`is-modal ${active}`} onClick={this.onCloseModel} id="modal">
				<div className="is-modal__block container" onClick={this.onCloseModel} id="modal">
					<div className="is-modal__inner" id="modal">
						<button className="is-modal__btn-close">
							<i className="fas fa-times is-modal__icon-close" id="modal"></i>
						</button>
						<ModalContent />
					</div>
				</div>
			</div>
		);
	}
}

const mapStateToProps = state => {
	return {
		modalActive: state.reducerModal,
		modalContent: state.reducerModalContent
	}
};

const mapDispatchToProps = dispatch => bindActionCreators({
	closeModal,
	closeModalContent
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Modal)