import * as types from '../actionType';

export const showModal = () => ({
	type: types.SHOW_MODAL,
	payload: true
});

export const closeModal = () => ({
	type: types.CLOSE_MODAL,
	payload: false
});

export const showModelContent = (arr) => ({
	type: types.CLOSE_MODAL_CONTENT,
	payload: arr
});

export const closeModalContent = () => ({
	type: types.CLOSE_MODAL_CONTENT,
	payload: []
});