import React, { Component } from 'react';

import './app.scss';
import Header from "../header";
import Modal from "../modal";
import Content from "../content";

class App extends Component {

	render() {


		return (
			<div className="wrapper">
				<Modal />
				<div className="container">
					<Header/>
					<Content />
				</div>
			</div>
		);
	}
}



export default App;