import React, {Component, Fragment} from 'react';

import './modal-content.scss';
import {connect} from "react-redux";
import ModalTechno from "../modal-techno";


class ModalContent extends Component {

	render() {

		const { modalContent }  = this.props;

		const info = modalContent.map(({name, img, technology, path, bgColor, colorText}, index) => {
			const data = {technology, bgColor, colorText};
			return (
				<Fragment key={index}>
					<div className="is-modal-content__info">
						<h2 className="is-modal-content__title">{name}</h2>
						<section className="is-modal-content__box">
							<div className="is-modal-content__info-block is-modal-content__list">
								<h5>Технологии</h5>
								<ModalTechno data={data}/>
							</div>
							<div className="is-modal-content__info-block is-modal-content__links">
								<h5>Ссылки</h5>
								<a
									className="is-modal-content__link"
									href={`https://isconst808.github.io/${path}/index.html`}
									target="_blank"
									rel="noreferrer noopener"
								>
									<i className="fas fa-external-link-alt">
									</i>
									<p>Открыть <span>{name}</span></p>
								</a>
								<a
									href={`https://github.com/isconst808/${path}`}
									className="is-modal-content__link"
									target="_blank"
									rel="noreferrer noopener"
								>
									<i className="fab fa-github">
									</i>
									<p>Перейти на <span>GitHub</span></p>
								</a>
							</div>
						</section>
					</div>
					<div className="is-modal-content__img-temp">
						<img src={img} alt={name}/>
					</div>
				</Fragment>
			);
		});

		return (
			<div className={`is-modal-content`}>
				{info}
			</div>
		);
	}
}

const mapStateToProps = state => {
	return {
		modalContent: state.reducerModalContent
	}
};

export default connect(mapStateToProps)(ModalContent)